import React from 'react';
import PropTypes from 'prop-types';
import Layout from 'components/layout';
import Box from 'components/box';
import Title from 'components/title';
import Gallery from 'components/gallery';
import Modal from 'containers/modal';
import { graphql } from 'gatsby';

const Index = ({ data }) => (
  <Layout>
    <Box>
      <Title as="" size="large">
        <div
          dangerouslySetInnerHTML={{
            __html: data.homeJson.content.childMarkdownRemark.html,
          }}
        />
      </Title>
      <Modal buttonText={data.homeJson.modal.buttonText}>
        <h2>{data.homeJson.modal.title}</h2>
        <div
          dangerouslySetInnerHTML={{
            __html: data.homeJson.modal.description.childMarkdownRemark.html,
          }}
        />

        <a
          href="https://docs.google.com/forms/d/e/1FAIpQLSe_9nQ2ABRe-lSvEj3YueEcoCACTnEXOfya_5CAZ97nA-YzAA/viewform"
          target="_blank"
          rel="noopener noreferrer"
        >
          Submit Design
        </a>
      </Modal>
    </Box>
    <Gallery items={data.homeJson.gallery} />
  </Layout>
);

Index.propTypes = {
  data: PropTypes.object.isRequired,
};

export default Index;

export const query = graphql`
  query HomepageQuery {
    homeJson {
      content {
        childMarkdownRemark {
          html
          rawMarkdownBody
        }
      }
      modal {
        buttonText
        title
        description {
          childMarkdownRemark {
            html
            rawMarkdownBody
          }
        }
      }
      gallery {
        title
        copy
        image {
          childImageSharp {
            fluid(maxHeight: 500, quality: 90) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    }
  }
`;
